import React from 'react'
import '../Styles.css/Style.css'

export function Disciplinas(){
    return(
        <div>
        <div className='SeccionTitulo'>
        <h2 className='Titulo'>Nuestro Servicio</h2>
        <p>Nosotros ofrecemos los mejores vehiculos para nuestros clientes durante su ronda.</p>
        </div>
        </div>
    )
}

export function Contenedores({Imagen, Titulo, Texto, NombreImagen, Clase, ClaseImagen}){
    return(
        <div className={Clase ? 'ContenedorGrid' : 'ContenedorFlex'}>
        <section className='Columnas'>
        <div className='ContenedorImagen'>
        <img className={ClaseImagen ? 'Imagen' : 'SeccionImagen'} src={require(`../Media/${Imagen}.jpg`)} alt={NombreImagen} />
        </div>
        </section>
        <section className={Clase ? 'Textoxd' : ''}>
        <h3>{Titulo}</h3>
        <p>{Texto}</p>
        <button className='BotonDetalles'>Mas Detalles</button>
        </section>
        </div>
    );
}

export function Contenedorees({Imagen, Titulo, Texto, Clase, NombreImagen}){
    return(
        <div className='GridMacauno'>
        <section className='SeccionImg'>
        <img className='ImagenOK' src={require(`../Media/${Imagen}.jpg`)} alt={NombreImagen} />
        </section>
        <section className={Clase ? 'Textoxd' : 'textoxd'}>
        <h3>{Titulo}</h3>
        <p>{Texto}</p>
        <button className='BotonDetalless'>Mas Detalles</button>
        </section>
        </div>
    );
}
