import React from 'react'
import { Formulario } from '../Componentes/Formulario';
import { Portada } from '../Componentes/Nosotros';
import '../Styles.css/Style.css'
import '../Styles.css/StyleNosotros.css'

function Contacto(){
    return(
        <div>
        <Portada Titulo='Contacto' Texto="Use the form below to get in touch with us. Share your specific requirements and we'll be happy to reply back shortly. You are also welcome to contact us via phone, emial or at our office during working hours." />
        <Formulario/>
        </div>
    );
}

export default Contacto;