import React from 'react'

export function Sidebar(){
    return(
        <div className='ContenedorSideBar'>
        <Lista Titulo='Meta' Lista1='Log In' Lista2='RSS' Lista3='Comentarios' Lista4='Wordpress.org'/>
        <Dato Titulo='Dato de la Semana' Texto='Six-month sales at BMW brand rose 8 percent to 804,000 cars, expanding the lead over runner-up Audi to 24,000 from 11,000 after five months. Half-year sales at Audi and Mercedes rose 6 percent each to 780,500 and 694,000 respectively.'/>
        <Lista Titulo='Recent Posts' Lista1='Dodge Charger, F-250 popular with car theives' Lista2='Dodge Charger, F-250 popular with car theives' Lista3='Dodge Charger, F-250 popular with car theives' Lista4='Dodge Charger, F-250 popular with car theives'/>
        </div>
    );
}

function Lista({Titulo, Lista1,Lista2,Lista3,Lista4}){
    return(
        <section>
            <h2>{Titulo}</h2>
            <ul className='Listas'>
                <li href='##'>{Lista1}</li>
                <li href='##'>{Lista2}</li>
                <li href='##'>{Lista3}</li>
                <li href='##'>{Lista4}</li>
            </ul>
        </section>
    );
}

function Dato({Titulo,Texto}) {
    return(
        <section id='Dato'>
            <h2>{Titulo}</h2>
            <p>{Texto}</p>
        </section>
    );
}
