import React from 'react'
import '../Styles.css/StyleContacto.css'
import { useForm } from 'react-hook-form';
import 'react-toastify/dist/ReactToastify.css';
export function Formulario() {

    const { register, formState: { errors }, handleSubmit } = useForm();

    const onSubmit = (data) => {
        console.log(data)
    }

    return (
        <div className='ContenedorFormulario'>
            <form className='Formulario' onSubmit={handleSubmit(onSubmit)}>
                <fieldset>
                    <legend>Contact Form</legend>
                    <div className='Campos'>
                        <div className='Campo'>
                            <input type="text" {...register('nombre', {
                                required: true,
                                maxLength: 10,
                                minLength: 4,
                                pattern: /[A-Za-z]+$/i
                            })} placeholder='Nombre' />
                            {errors.nombre?.type === 'required' && <p className='Warning'>Este campo es obligatorio</p>}
                            {errors.nombre?.type === 'maxLength' && <p className='Warning'>El maximo de caracteres es de 10</p>}
                            {errors.nombre?.type === 'minLength' && <p className='Warning'>El minimo de caracteres es de 4</p>}
                            {errors.nombre?.type === 'pattern' && <p className='Warning'>Este campo solo permite Letras</p>}
                        </div>
                        <div className='Campo'>
                            <input type='email' {...register('email', {
                                required: true,
                                pattern: /[^@ \t\r\n]+@[^@ \t\r\n]+\.[^@ \t\r\n]+$/i
                            })} placeholder='Enter Email' />
                            {errors.email?.type === 'pattern' && <p className='Warning'>El formato del Mail es incorrecto.</p>}
                            {errors.email?.type === 'required' && <p className='Warning'>Este campo es obligatorio.</p>}
                        </div>
                        <div className='Campo'>
                            <textarea {...register('mensaje', {
                                required: true,
                                maxLength: 200,
                                minLength: 20
                            })} placeholder='Your Message' />
                            {errors.mensaje?.type === 'required' && <p className='Warning'>Este campo es obligatorio</p>}
                            {errors.mensaje?.type === 'maxLength' && <p className='Warning'>El maximo de caracteres es de 200</p>}
                            {errors.mensaje?.type === 'minLength' && <p className='Warning'>El minimo de caracteres es de 20</p>}
                        </div>
                        <div className='Campo'>
                            <input id='submit' type='submit' value='Submit' name='Send' />
                        </div>
                    </div>
                </fieldset>
            </form>
            <section >
            <section>
                <h3>Contact Info</h3>
                <h6>SED UT PERSPICIATIS UNDE OMNIS ISTE NATUS ERROR SIT VOLUPTATEM ACCUSANTIUM DOLOREMQUE.</h6>
                <p>Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque la udantium, totam rem aperiam, eaque ipsa quae ab illo inventore quasi. Nemo enim ipsam voluptatem quia.</p>
                </section>
                <section >
                <ul id='ContactoPersonal'>
                    <p>JL.Kemacetan timur no.23. block.Q3</p>
                    <p>Jakarta-Indonesia</p>
                    <p>+6221 888 888 90</p>
                    <p>+6221 888 88891</p>
                    <p>info@yourdomain.com</p>
                    </ul>
                </section>
            </section>
            
        </div>
    )
}