import React from 'react'
import '../Styles.css/Style.css'
export function Servicios(){
    return(
        <div className='ContenedorServicios'>
        <h2 className='Titulo'>Servicios</h2>

        <div className='CajaServicios'>
        <section className='ServiciosFlex'>
        <Iconos 
            Imagen = 'resultado'
            Titulo = 'Consigue Resultados'
            Texto = 'Sed ut perspiciatis unde om nis natus error sit volup atem accusant dolorem que laudantium.'
            NombreImagen = 'Resultados'
        />
        </section>
        <section className='ServiciosFlex'>
        <Iconos 
            Imagen = 'tuercas'
            Titulo = 'Alta Tecnologia'
            Texto = 'Sed ut perspiciatis unde om nis natus error sit volup atem accusant dolorem que laudantium.'
            NombreImagen = 'Tecnologia'
        />
        </section>
        <section className='ServiciosFlex'>
        <Iconos 
            Imagen = 'winner'
            Titulo = 'Creando Ganadores'
            Texto = 'Sed ut perspiciatis unde om nis natus error sit volup atem accusant dolorem que laudantium.'
            NombreImagen = 'Meta'
        />
        </section>
        <section className='ServiciosFlex'>
        <Iconos 
            Imagen = 'tinder'
            Titulo = 'Alta Perfomance'
            Texto = 'Sed ut perspiciatis unde om nis natus error sit volup atem accusant dolorem que laudantium.'
            NombreImagen = 'Exito'
        />
        </section>
        </div>
        </div>
    );
}

export function Iconos({Imagen, Titulo, Texto, NombreImagen}){
    return(
        <section className='Serviciosflex'>
        <img className='Ico' src={require(`../Media/${Imagen}.jpg`)} alt={NombreImagen} />
        <h4>{Titulo}</h4>
        <p>{Texto}</p>
        </section>
    );
}
