import React from 'react'
import '../Styles.css/StyleNosotros.css'

export function Portada ({Titulo, Texto}){
    return(
        <div className='ContenedorPortada'>
            <h1>{Titulo}</h1>
            <p>{Texto}</p>
        </div>
    );
}

export function Entrada({titulo, imagen, texto, textoImagen}){
    let date = new Date();
    let output = String(date.getDate()).padStart(2, '0') + '/' + String(date.getMonth() + 1).padStart(2, '0') + '/' + date.getFullYear();
    
    return(
        <div>
        <section>
            <h3>{titulo}</h3>
            <p>Posteado el: {output}</p>
        </section>
        <div>
        <div className='ContenedorEntrada'>
            <section>
                <img src={require(`../Media/${imagen}.jpg`)} alt={textoImagen}/>
            </section>
            <section className='ContenedorTexto'>
                <p>{texto}</p>
            </section>
            </div>
            <button className='BotonMas'>Read More...</button>
            <hr/>
            
        </div>
        </div>
    );
}

